<template>
    <nacl-page-container no-footer bo-header>
        <!-- <nacl-header style="color: #000;">{{ title ? title : keys }}</nacl-header> -->
        <nacl-page-content id="home">
            <commodity-list
                :list-data="listData"
                :no-data="noData"
                :busy="busy"
                :offer-pric="offerPric"
                @loadMore="loadMore"
            />
        </nacl-page-content>
        <p v-if="noData" class="prompt">暂无更多内容了～</p>
        <cart />
    </nacl-page-container>
</template>

<script>
import commodityList from '../components/commodity/commodity-list'
import cart from '../components/cart'
import axios from 'axios'

export default {
    name: 'home',
    components: {
        commodityList,
        cart
    },
    data () {
        console.log(this.$route.params.keys)
        return {
            title: this.$route.query.title,
            keys: this.$route.params.keys,
            listData: [],
            noData: false,
            pageIndex: 0,
            busy: false,
            offerPric: '9.9'
        }
    },
    computed: {
    },
    created () { },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        getList () {
            const params = {
                pageIndex: this.pageIndex,
                pageSize: 20,
                keys: this.keys
            }
            axios.get('http://47.106.209.243:5000/api/commodity/getOfferList', { params }).then((rq) => {
                // this.listData = rq.data.data
                const listData = rq.data.data
                if (listData.length === 0) {
                    this.noData = true
                }
                this.listData = this.listData.concat(listData)
                this.busy = false
            })
        },
        loadMore () {
            if (!this.noData) {
                this.busy = true
                this.pageIndex++
                this.getList()
            }
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>
<style lang="scss" scoped>
#home {
    overflow: auto;
}
.prompt {
    margin: 20px 0;
    font-size: 30px;
    color: #666;
    text-align: center;
}
</style>
